/* GENERAL */
  *{
      margin:0;
      padding:0;
  }
  html, body{
      width:100%;
      height:100%;
      position:relative;
  }
  body{
      background:#D5E3E3;
      font-size:14px;
      color:#111725;
      overflow-x:hidden;
      font-family:'Times New Roman';
      letter-spacing:1px;
  }
  @media only screen and (max-width:768px){
    body{
      overflow-x:auto;
    }
  }
/* END GENERAL */

/* COMMONS */
  div#wrapper{
      min-width:320px;
  }
  div.contentWrapper{
    padding:15px;
  }
  .clearfix, .cf{
      clear:both;
  }
  h1{
    font-family:'Courier New';
    letter-spacing:0;
  }
  .moustache{
    position:relative;
  }
  .moustache:after{
    content:'{';
    position:absolute;
    top:-110px;
    left:-25px;
    font-size:180px;
    font-weight:normal;
    transform: rotate(50deg);
    font-family:"Times New Roman";
  }

/* COMMONS-Z-INDEX */
  div#topBanner,
  nav,
  div#notification{
    z-index:100;
  }
  div#topBanner form input[type="submit"],
  nav ul#navigationMenu>li>ul#hiddenMenuItems{
    z-index:101;
  }
  /*{
    z-index:102;
  }*/
  div#loading{
    z-index:9999;
  }
/* END COMMONS-Z-INDEX */

/* COMMONS-COLOR */

/* END COMMONS-COLOR */

/* COMMONS-FORM */
  input{
    font-family:'Times New Roman';
    outline:none;
  }
  input:not([type="submit"]){
    letter-spacing:2px;
  }
  label{
    letter-spacing:2px;
    font-size:12px;
  }
  input[type="submit"]{
    border:2px solid #111725;
    background:none;
    width:50%;
    margin:auto;
    height:30px;
    display:block;
    cursor:pointer;
    letter-spacing:1px;
  }
  input[type="submit"]:hover{
    background:#111725;
    color:#D5E3E3;
  }
  label.moveLabelToBottom{
    animation-name: moveLabelToBottom;
    animation-duration: 0.2s;
    animation-timing-function: ease-in-out;
    position:absolute;
    left:5px;
    opacity:0.7;
    z-index:-1;
    top:40px;
    font-size:11px;

  }
  @keyframes moveLabelToBottom{
    from { top:8px; }
    to   { top:40px; }
  }
  label.labelOnBottom{
    position:absolute;
    left:5px;
    opacity:0.7;
    z-index:-1;
    top:40px;
    font-size:11px;
  }
/* END COMMONS-FORM */

/* COMMONS-LINK */
  a{
      color:#111725;
      text-decoration:underline;
  }
/* END COMMONS-LINK */

/* COMMONS-LIST */
  ul{
      list-style:none;
  }
/* END COMMONS-LINKS */

/* COMMONS-HOVER-EFFECT */
  .menuIco.isHover img{
    animation-name: menuIcoIsHover;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;
    transform:scale(1.5);
    background: #577F7F;
  }
  @keyframes menuIcoIsHover{
    from { transform:scale(1); background: #111725; }
    to   { transform:scale(1.5); background: #577F7F; }
  }
  @media only screen and (min-width:768px){
    .menuIco.isHover img{
        animation-name: menuIcoIsHover;
        animation-duration: 0.5s;
        animation-timing-function: ease-in-out;
        transform:scale(1.4);
        background: #577F7F;
    }
    @keyframes menuIcoIsHover{
      from { transform:scale(1); background: #111725; }
      to   { transform:scale(1.4); background: #577F7F; }
    }
  }
/* END COMMONS-HOVER-EFFECT */

/* END COMMONS */

/* LOADING */
  div#loading{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    overflow:hidden;
    background:rgba(0,0,0,0.85);
  }
  div#loading span#loadingIco{
      left:50%;
      top:50%;
      margin-left:-40px;
      margin-top:-40px;
      position: absolute;
      width: 40px;
      height: 40px;
      border: 8px solid #fff;
      border-right-color: transparent;
      border-radius: 50%;
      box-shadow: 0 0 25px 2px #eee;
      animation: spin 1s linear infinite;
  }
  @keyframes spin{
      from { transform: rotate(0deg);   opacity: 0.2; }
      50%  { transform: rotate(180deg); opacity: 1.0; }
      to   { transform: rotate(360deg); opacity: 0.2; }
  }
/* END LOADING */

/* NOTIFICATION */
  div#notification{
      position:fixed;
      top:0;
      left:0;
      width:100%;
  }
  div#notification ul{
      width:100%;
  }
  div#notification ul li{
      position:relative;
      display:block;
      margin:auto;
      margin-top:5px;
      cursor:pointer;
      border-radius:8px;
      padding:7px;
      font-weight:bold;
      border-bottom:1px solid #000;
      text-align:justify;
  }
  div#notification ul li.error{
      background:rgba(252,201,201,0.9);
      color:#F36F6F;
      border:2px solid #F36F6F;
  }
  div#notification ul li.success{
      background:rgba(186,252,167,0.9);
      color:#08A54B;
      border:2px solid #08A54B;
  }
  div#notification ul:hover:after{
    content: attr(data-title);
    color:#D5E3E3;
    position:absolute;
    bottom:-20px;
    right:0;
  }
  @media only screen and (min-width:768px){
    div#notification{
        top:auto;
        left:auto;
        bottom:78px;
        right:22px;
        width:300px;
    }
    div#notification ul li{
        width:100%;
        margin-bottom:5px;
    }
  }
/* END NOTIFICATION */

/* TOP-BANNER */
  div#topBanner{
      background:#111725;
      height:30px;
      width:100%;
      position:fixed;
  }
  div#topBanner form{
      float:left;
      position:relative;
  }
  div#topBanner form input[type="text"]{
      height:25px;
      width:200px;
      margin-top:2px;
      margin-left:2px;
      padding:0 5px 0 5px;
      cursor:pointer;
      border-radius:3px;
      border:none;
      box-shadow: inset 0 0 5px #111725;
  }
  div#topBanner form input[type="submit"]{
      background:url('../images/topSearchFormSubmitIco.png');
      background-size:25px 25px;
      border:none;
      position:absolute;
      width:25px;
      height:25px;
      right:0;
      top:2px;
      cursor:pointer;
  }
  div#topBanner span#pageTitle{
    color:#D5E3E3;
    margin-left:25px;
    font-size:24px;
  }
  @media only screen and (max-width:768px){
    div#topBanner span#pageTitle{
      display:none;
    }
  }
  div#topBanner div#topAddButton{
      float:right;
      color:#D5E3E3;
      font-size:24px;
      cursor:pointer;
  }
  div#topBanner div#topAddButton span{
      padding:5px 10px;
  }
  div#topBanner div#topAddButton:hover{
      background:#577F7F;
  }
  div#topBanner div#topAddButton:hover:after{
      content: attr(data-title);
      padding:8px;
      background:#111725;
      position: absolute;
      right:0;
      top:27px;
      white-space: nowrap;
      border:5px solid #577F7F;
      font-size:14px;
  }
/* END TOP-BANNER */

/* BOTTOM-MENU */
  nav{
      background:#111725;
      position:fixed;
      bottom:0;
      left:0;
      width:100%;
      border-bottom:5px solid #577F7F;
      border-top:5px solid #577F7F;
  }
  nav ul#navigationMenu>li{
      float:left;
      height:55px;
      width:55px;
      position:relative;
      cursor:pointer;
      padding:8px;
  }
  nav ul#navigationMenu>li img{
      width:55px;
      height:55px;
  }
  nav ul#navigationMenu>li>ul#hiddenMenuItems{
    position:absolute;
    top:0;
    left:-3px;
    padding:5px;
    text-align: center;
    background:#111725;
    width:200px;
    display:none;
    box-shadow:-2px -2px 2px #111725;
    border:5px solid #577F7F;
  }
  nav ul#navigationMenu>li>ul#hiddenMenuItems a{
    color:#D5E3E3;
    text-decoration:none;
  }
  nav ul#navigationMenu>li>ul#hiddenMenuItems a:hover{
    color:#577F7F;
  }
  nav ul#navigationMenu>li>ul#hiddenMenuItems.active{
    display: block;
  }
  nav ul#navigationMenu>li>ul#hiddenMenuItems li{
    padding:5px 10px 5px 10px;
    border-bottom:1px dotted #D5E3E3;
    margin:5px;
  }
  nav ul#navigationMenu>li>ul#hiddenMenuItems li:hover{
    border-color:#577F7F;
  }
  @media only screen and (min-width:768px){
    nav ul#navigationMenu>li:after{
      padding:8px;
      background:#111725;
      position: absolute;
      text-align: center;
      left:-3px;
      bottom:72px;
      width:179px;
      white-space: nowrap;
      border:5px solid #577F7F;
      border-left-width: 8px;
      color:#D5E3E3;
      content: attr(data-title);
      opacity: 0;
      display:none;
    }
    nav ul#navigationMenu>li:hover:after{
      opacity:1;
      display:block;
      animation-name: menuItemDataTitle;
      animation-duration:0.6s;
    }
    @keyframes menuItemDataTitle{
      from{opacity:0;}
      to{opacity:1;}
    }
    nav ul#navigationMenu>li.hideDataTitle:after{
      opacity:0;
      display:none;
    }
  }
/* END BOTTOM-MENU */

/* MIDDLE-SECTION */
  section#content{
      position:relative;
      padding:30px 0 80px 0;
  }

/* MIDDLE-SECTION-LOGIN-FORM-REGISTER-FORM */
  section#content div.authForm{
    width:75%;
    margin:35px auto;
  }
  @media only screen and (min-width:768px){
    section#content div.authForm{
      width:500px;
    }
  }
  section#content div.authForm h1{
    padding:5px;
    border-bottom:3px solid #6D9E9E;
  }
  section#content div.authForm form{
    padding:8px;
    margin-top:25px;
  }
  section#content div.authForm form p{
    display:block;
    width:100%;
    position:relative;
    margin-bottom:25px;
  }
  section#content div.authForm form p input:not([type="submit"]){
    width:100%;
    height:35px;
    cursor:pointer;
    border:none;
    background:none;
    border-bottom:1px solid #456B6B;
    padding-left:20px;
    font-size:16px;
  }
  section#content div.authForm form p input:not([type="submit"]):hover{
    border-bottom:1px solid #111725;
  }
  section#content div.authForm form p input.inputError:not([type="submit"]){
  border-bottom:2px solid #CD0100;
  }
  section#content div.authForm form p label:not(.moveLabelToBottom):not(.labelOnBottom){
    position:absolute;
    top:8px;
    left:5px;
    opacity:0.7;
    z-index:-1;
  }
/* END MIDDLE-SECTION-LOGIN-FORM-REGISTER-FORM */

/* MIDDLE-SECTION-USER-ACCOUNT-MANAGE */
  section#content div#userAccountManage>div{
    background:#577F7F;
    width:500px;
    box-shadow:0 0 3px #111725;
    color:#111725;
    border:2px solid #111725;
  }
  section#content div#userAccountManage form{
    margin-bottom:25px;
    text-align:center;
    padding-bottom:15px;
  }
  section#content div#userAccountManage form h2{
    margin-bottom:10px;
    padding:15px;
  }
  section#content div#userAccountManage form input:not([type="submit"]){
    display:block;
    width:80%;
    height:25px;
    padding-left:8px;
    margin:auto;
    margin-bottom:15px;
    border-radius:5px;
    border:1px solid #52636B;
    background:#F2F2F2;
  }
  section#content div#userAccountManage form input:not([type="submit"]):focus{
    box-shadow:0 0 2px black;
  }
/* END MIDDLE-SECTION-USER-ACCOUNT-MANAGE */

/* END MIDDLE-SECTION */